import React, { useState } from 'react';
import { Search, User, MessageSquare } from 'lucide-react';
import { useAuth } from '../../contexts/AuthContext';

const ChatSidebar = ({ onSelectUser, selectedUserId, users, loading, onlineUsers }) => {
    const { user: currentUser } = useAuth();
    const [searchTerm, setSearchTerm] = useState('');

    const filteredUsers = Array.isArray(users) ? users.filter(user => {
        if (!user) return false;
        // Exclude current user
        const currentUserId = currentUser?._id || currentUser?.id;
        const userId = user._id || user.id;
        if (currentUserId === userId) return false;

        const firstName = user.firstName || '';
        const lastName = user.lastName || '';
        const email = user.email || '';
        const name = `${firstName} ${lastName}`.toLowerCase();
        const search = (searchTerm || '').toLowerCase();
        return name.includes(search) || email.toLowerCase().includes(search);
    }) : [];

    const formatTime = (dateString) => {
        if (!dateString) return '';
        const date = new Date(dateString);
        return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
    };

    return (
        <div className="chat-sidebar d-flex flex-column h-100 bg-white border-end">
            <div className="p-3 border-bottom bg-light">
                <h5 className="mb-3">Conversations</h5>
                <div className="position-relative">
                    <Search className="position-absolute top-50 start-0 translate-middle-y ms-2 text-muted" size={16} />
                    <input
                        type="text"
                        className="form-control form-control-sm ps-5"
                        placeholder="Search users..."
                        value={searchTerm}
                        onChange={(e) => setSearchTerm(e.target.value)}
                    />
                </div>
            </div>

            <div className="flex-grow-1 overflow-auto">
                {loading ? (
                    <div className="p-4 text-center">
                        <div className="spinner-border spinner-border-sm text-primary" role="status"></div>
                    </div>
                ) : filteredUsers.length === 0 ? (
                    <div className="p-4 text-center text-muted small">No users found</div>
                ) : (
                    <div className="list-group list-group-flush">
                        {filteredUsers.map(user => (
                            <button
                                key={user._id}
                                className={`list-group-item list-group-item-action p-3 d-flex align-items-center gap-3 border-0 ${selectedUserId === user._id ? 'bg-primary-light active-chat-user' : ''}`}
                                onClick={() => onSelectUser(user)}
                                style={{ transition: 'all 0.2s ease' }}
                            >
                                <div className="position-relative">
                                    {user.avatar ? (
                                        <img
                                            src={user.avatar}
                                            alt={user.firstName}
                                            className="rounded-circle"
                                            style={{ width: '40px', height: '40px', objectFit: 'cover' }}
                                        />
                                    ) : (
                                        <div className="bg-primary text-white rounded-circle d-flex align-items-center justify-content-center" style={{ width: '40px', height: '40px', fontWeight: 'bold' }}>
                                            {user.firstName.charAt(0)}{user.lastName.charAt(0)}
                                        </div>
                                    )}
                                    {onlineUsers.includes(user._id) && (
                                        <span className="position-absolute bottom-0 end-0 bg-success border border-white rounded-circle" style={{ width: '12px', height: '12px' }}></span>
                                    )}
                                </div>
                                <div className="flex-grow-1 min-w-0">
                                    <div className="d-flex justify-content-between align-items-center mb-1">
                                        <h6 className="mb-0 text-truncate font-weight-bold" style={{ fontSize: '0.9rem' }}>
                                            {user.firstName} {user.lastName}
                                        </h6>
                                        {user.lastMessage && (
                                            <span className="text-muted" style={{ fontSize: '0.7rem' }}>
                                                {formatTime(user.lastMessage.createdAt)}
                                            </span>
                                        )}
                                    </div>
                                    <div className="d-flex justify-content-between align-items-center">
                                        <p className="mb-0 text-muted text-truncate small" style={{ maxWidth: '140px' }}>
                                            {user.lastMessage ? user.lastMessage.content : user.role?.name || 'User'}
                                        </p>
                                        {user.unreadCount > 0 && (
                                            <span className="badge bg-danger rounded-pill" style={{ fontSize: '0.65rem' }}>
                                                {user.unreadCount}
                                            </span>
                                        )}
                                    </div>
                                </div>
                            </button>
                        ))}
                    </div>
                )}
            </div>
        </div>
    );
};

export default ChatSidebar;
