import React, { useState, useEffect } from 'react';
import { MessageSquare } from 'lucide-react';
import { useNavigate } from 'react-router-dom';
import ChatSidebar from './ChatSidebar';
import ActionChat from '../actions/ActionChat';
import { useAuth } from '../../contexts/AuthContext';
import api from '../../services/api';
import { useSocket } from '../../contexts/SocketContext';

const ChatContainer = () => {
    const { user: currentUser } = useAuth();
    const navigate = useNavigate();
    const [selectedUser, setSelectedUser] = useState(null);
    const [users, setUsers] = useState([]);
    const [usersLoading, setUsersLoading] = useState(true);
    const [messages, setMessages] = useState([]);
    const socket = useSocket();
    const [onlineUsers, setOnlineUsers] = useState([]);
    const [tasks, setTasks] = useState([]);

    useEffect(() => {
        fetchUsers();
    }, []);

    const fetchUsers = async () => {
        try {
            setUsersLoading(true);
            const response = await api.get('/chat/users');
            if (response.data) {
                const userData = Array.isArray(response.data)
                    ? response.data
                    : (response.data.users || []);
                setUsers(userData);
            }
        } catch (error) {
            console.error('Failed to fetch chat users:', error);
            try {
                const fallbackRes = await api.get('/users');
                const fallbackData = Array.isArray(fallbackRes.data)
                    ? fallbackRes.data
                    : (fallbackRes.data.users || []);
                setUsers(fallbackData);
            } catch (innerError) {
                console.error('Fallback fetch failed:', innerError);
            }
        } finally {
            setUsersLoading(false);
        }
    };

    // Socket listeners for user status and new messages (for sidebar updates)
    useEffect(() => {
        if (!socket) return;

        const handleStatusChange = ({ userId, status }) => {
            setOnlineUsers(prev => {
                if (status === 'online') {
                    return prev.includes(userId) ? prev : [...prev, userId];
                } else {
                    return prev.filter(id => id !== userId);
                }
            });
        };

        const handleOnlineUsers = (users) => {
            setOnlineUsers(users);
        };

        const handleNewMessage = (message) => {
            // Update users list with last message and unread count
            setUsers(prevUsers => {
                return prevUsers.map(u => {
                    const userId = u._id || u.id;
                    if (userId === message.sender._id || userId === message.recipient._id) {
                        const isOtherSide = userId === message.sender._id;
                        return {
                            ...u,
                            lastMessage: message,
                            unreadCount: isOtherSide && (!selectedUser || (selectedUser._id !== userId && selectedUser.id !== userId))
                                ? (u.unreadCount || 0) + 1
                                : u.unreadCount
                        };
                    }
                    return u;
                });
            });

            // Update messages if conversation is active
            if (selectedUser && (
                (message.sender._id === selectedUser._id && message.recipient._id === (currentUser._id || currentUser.id)) ||
                (message.sender._id === (currentUser._id || currentUser.id) && message.recipient._id === selectedUser._id)
            )) {
                setMessages(prev => {
                    if (prev.some(m => (m._id || m.id) === (message._id || message.id))) return prev;
                    return [...prev, message];
                });
            }
        };

        socket.on('user_status_change', handleStatusChange);
        socket.on('online_users', handleOnlineUsers);
        socket.on('private-message', handleNewMessage);

        return () => {
            socket.off('user_status_change', handleStatusChange);
            socket.off('online_users', handleOnlineUsers);
            socket.off('private-message', handleNewMessage);
        };
    }, [socket, selectedUser, currentUser]);

    // Handle deep linking via userId query param - Silent & Resilient
    useEffect(() => {
        const urlParams = new URLSearchParams(window.location.search);
        const userId = urlParams.get('userId');

        if (userId && !usersLoading && users.length > 0) {
            // Check if user exists in our pre-loaded list
            const foundUser = users.find(u => (u._id || u.id) === userId);

            if (foundUser) {
                // Only select if not already selected
                if (!selectedUser || (selectedUser._id !== userId && selectedUser.id !== userId)) {
                    setSelectedUser(foundUser);
                }
            } else {
                // User not found in list, and since /chat/users returns ALL users, 
                // this means the ID is invalid or stale.
                console.warn('User not found for deep link, clearing URL:', userId);
                navigate('/chat', { replace: true });
            }
        }
    }, [window.location.search, users, usersLoading, navigate, selectedUser]);

    useEffect(() => {
        if (!socket || !currentUser) return;

        const handlePrivateMessage = (message) => {
            // Update messages if conversation is active
            if (selectedUser && (
                (message.sender._id === selectedUser._id && message.recipient._id === (currentUser._id || currentUser.id)) ||
                (message.sender._id === (currentUser._id || currentUser.id) && message.recipient._id === selectedUser._id)
            )) {
                setMessages(prev => {
                    if (prev.some(m => (m._id || m.id) === (message._id || message.id))) return prev;
                    return [...prev, message];
                });
            }
        };

        socket.on('private-message', handlePrivateMessage);

        return () => {
            socket.off('private-message', handlePrivateMessage);
        };
    }, [socket, currentUser, selectedUser]);

    useEffect(() => {
        if (selectedUser) {
            fetchConversation(selectedUser._id);
        }
    }, [selectedUser]);

    const fetchConversation = async (recipientId) => {
        try {
            const response = await api.get(`/chat/conversation/${recipientId}`);
            if (response.data) {
                setMessages(response.data);
            }
        } catch (error) {
            console.error('Failed to fetch conversation:', error);
        }
    };

    const handleSendMessage = async (content) => {
        if (!selectedUser) return;
        try {
            const response = await api.post('/chat/send', {
                recipientId: selectedUser._id,
                content
            });
            // We don't need to manually update state as socket will emit 'private-message' back to us
        } catch (error) {
            console.error('Failed to send message:', error);
        }
    };

    return (
        <div className="container-fluid p-0" style={{ height: 'calc(100vh - 100px)' }}>
            <div className="row g-0 h-100 border rounded overflow-hidden shadow-sm bg-white">
                <div className="col-md-4 col-lg-3 h-100">
                    <ChatSidebar
                        users={users}
                        loading={usersLoading}
                        onlineUsers={onlineUsers}
                        selectedUserId={selectedUser?._id || selectedUser?.id}
                        onSelectUser={setSelectedUser}
                    />
                </div>
                <div className="col-md-8 col-lg-9 h-100 bg-light d-flex flex-column">
                    {selectedUser ? (
                        <>
                            <div className="p-3 bg-white border-bottom d-flex align-items-center gap-3">
                                {selectedUser.avatar ? (
                                    <img src={selectedUser.avatar} className="rounded-circle" style={{ width: '32px', height: '32px' }} />
                                ) : (
                                    <div className="bg-primary text-white rounded-circle d-flex align-items-center justify-content-center" style={{ width: '32px', height: '32px', fontSize: '0.8rem' }}>
                                        {selectedUser.firstName.charAt(0)}{selectedUser.lastName.charAt(0)}
                                    </div>
                                )}
                                <h6 className="mb-0">{selectedUser.firstName} {selectedUser.lastName}</h6>
                            </div>
                            <div className="flex-grow-1 overflow-hidden">
                                <ActionChat
                                    messages={messages}
                                    onSendMessage={handleSendMessage}
                                    tasks={tasks}
                                    onNavigateToAction={(id) => navigate(`/actions?id=${id}`)}
                                />
                            </div>
                        </>
                    ) : (
                        <div className="h-100 d-flex flex-column align-items-center justify-content-center text-muted p-5 text-center">
                            <div className="mb-4 bg-white rounded-circle shadow-sm p-4">
                                <MessageSquare size={48} className="text-primary opacity-50" />
                            </div>
                            <h5>Select a user to start chatting</h5>
                            <p className="small">Select a contact from the sidebar to begin your direct conversation in real-time.</p>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
};

export default ChatContainer;
