import React from 'react';

const KPICard = ({ title, value, icon, trend, trendType = 'neutral' }) => {
  const getTrendClass = () => {
    if (trendType === 'positive') return 'trend-positive';
    if (trendType === 'negative') return 'trend-negative';
    return 'trend-neutral';
  };

  const getTrendIcon = () => {
    if (trendType === 'positive') return 'â†‘';
    if (trendType === 'negative') return 'â†“';
    return 'â†’';
  };

  return (
    <div className="kpi-card glass-card">
      <div className="kpi-footer" style={{ background: trendType === 'positive' ? 'var(--color-success-bg)' : trendType === 'negative' ? 'var(--color-error-bg)' : 'var(--color-info-bg)' }}>
        <i className={`bi ${icon}`} style={{ color: trendType === 'positive' ? 'var(--color-success)' : trendType === 'negative' ? 'var(--color-error)' : 'var(--color-info)' }}></i>
      </div>
      <div className="kpi-info">
        <div className="kpi-title">{title}</div>
        <div className="kpi-value">{value}</div>
        <div className={`kpi-trend ${getTrendClass()}`}>
          <span className="trend-icon">{getTrendIcon()}</span>
          <span>{trend}%</span>
          <span className="ms-1 text-muted small fw-normal">vs last month</span>
        </div>
      </div>
    </div>
  );
};

export default KPICard;
