import React, { useState, useEffect } from 'react';

const DataTable = ({
  data = [],
  columns = [],
  title = 'Data Table',
  searchable = false,
  sortable = false,
  pagination = false,
  pageSize = 10
}) => {
  const [tableData, setTableData] = useState([]);
  const [filteredData, setFilteredData] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [sortConfig, setSortConfig] = useState({ key: null, direction: 'asc' });
  const [currentPage, setCurrentPage] = useState(1);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    if (data && data.length > 0) {
      setTableData(data);
      setFilteredData(data);
    }
    setIsLoading(false);
  }, [data]);

  useEffect(() => {
    if (searchTerm) {
      const filtered = tableData.filter(item =>
        Object.values(item).some(val =>
          String(val).toLowerCase().includes(searchTerm.toLowerCase())
        )
      );
      setFilteredData(filtered);
    } else {
      setFilteredData(tableData);
    }
    setCurrentPage(1);
  }, [searchTerm, tableData]);

  const handleSort = (key) => {
    if (!sortable) return;

    let direction = 'asc';
    if (sortConfig.key === key && sortConfig.direction === 'asc') {
      direction = 'desc';
    }

    const sorted = [...filteredData].sort((a, b) => {
      if (a[key] < b[key]) return direction === 'asc' ? -1 : 1;
      if (a[key] > b[key]) return direction === 'asc' ? 1 : -1;
      return 0;
    });

    setFilteredData(sorted);
    setSortConfig({ key, direction });
  };

  const getDisplayData = () => {
    if (!pagination) return filteredData;
    const start = (currentPage - 1) * pageSize;
    return filteredData.slice(start, start + pageSize);
  };

  const getColumnHeaders = () => {
    if (columns.length > 0) return columns;
    if (filteredData.length > 0) return Object.keys(filteredData[0]);
    return [];
  };

  const formatValue = (value, column) => {
    if (typeof value === 'number') {
      if (column.includes('revenue') || column.includes('spend') || column.includes('sales') || column.includes('aov')) {
        return 'â‚¹' + value.toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 });
      }
      if (column.includes('acos') || column.includes('roas') || column.includes('conversion') || column.includes('ctr')) {
        if (column.includes('roas')) return value.toFixed(2) + 'x';
        return value.toFixed(1) + '%';
      }
      if (column.includes('units') || column.includes('impressions') || column.includes('clicks') || column.includes('sessions') || column.includes('reviews')) {
        return value.toLocaleString();
      }
      return value;
    }
    return value;
  };

  const formatHeader = (column) => {
    return column
      .replace(/([A-Z])/g, ' $1')
      .replace(/_/g, ' ')
      .replace(/^./, str => str.toUpperCase());
  };

  if (isLoading) {
    return (
      <div className="d-flex justify-content-center align-items-center p-6" style={{ minHeight: '200px' }}>
        <div className="loading-spinner"></div>
      </div>
    );
  }

  if (filteredData.length === 0) {
    return (
      <div className="data-table-container">
        <div className="data-table-header">
          <h3 className="data-table-title">{title}</h3>
        </div>
        <div className="text-center p-6" style={{ minHeight: '200px' }}>
          <i className="bi bi-inbox fs-1 text-muted mb-3 d-block"></i>
          <p className="text-muted mb-0">No data available</p>
        </div>
      </div>
    );
  }

  const headers = getColumnHeaders();

  return (
    <div className="data-table-container">
      <div className="data-table-header">
        <h3 className="data-table-title">{title}</h3>
        {searchable && (
          <div className="d-flex align-items-center gap-2">
            <i className="bi bi-search text-muted"></i>
            <input
              type="text"
              className="form-control form-control-sm"
              placeholder="Search..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              style={{ width: '200px' }}
            />
          </div>
        )}
      </div>
      <div style={{ overflowX: 'auto' }}>
        <table className="data-table">
          <thead>
            <tr>
              {headers.map((col, idx) => (
                <th
                  key={idx}
                  onClick={() => handleSort(col)}
                  style={{ cursor: sortable ? 'pointer' : 'default' }}
                >
                  {formatHeader(col)}
                  {sortable && sortConfig.key === col && (
                    <i className={`bi bi-chevron-${sortConfig.direction === 'asc' ? 'up' : 'down'} ms-2`}></i>
                  )}
                </th>
              ))}
            </tr>
          </thead>
          <tbody>
            {getDisplayData().map((item, rowIdx) => (
              <tr key={rowIdx}>
                {headers.map((col, colIdx) => (
                  <td key={colIdx}>
                    {formatValue(item[col], col)}
                  </td>
                ))}
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      {pagination && filteredData.length > pageSize && (
        <div className="d-flex justify-content-between align-items-center p-3 border-top" style={{ borderColor: 'var(--color-gray-200)' }}>
          <span className="text-muted fs-sm">
            Showing {((currentPage - 1) * pageSize) + 1} to {Math.min(currentPage * pageSize, filteredData.length)} of {filteredData.length} entries
          </span>
          <div className="d-flex gap-2">
            <button
              className="btn btn-sm btn-secondary"
              disabled={currentPage === 1}
              onClick={() => setCurrentPage(currentPage - 1)}
            >
              <i className="bi bi-chevron-left"></i> Previous
            </button>
            <button
              className="btn btn-sm btn-secondary"
              disabled={currentPage * pageSize >= filteredData.length}
              onClick={() => setCurrentPage(currentPage + 1)}
            >
              Next <i className="bi bi-chevron-right"></i>
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default DataTable;
